
.logo {
  width: 180px;
  position: absolute;
  left: 0;
  top: 12px;
  cursor: pointer;
}
.p{
    cursor: pointer;
}
.subProduct {
  left: 435px;
  height: 90px !important;
  z-index: 999;
  position: absolute;
  top: 110px;
  width: 120px;
  background-color: rgba(255, 255, 255, .8);
  overflow: hidden;
  -webkit-animation: enterPro .5s;
          animation: enterPro .5s;
}
.nav-item {
  margin: 0;
  padding: 0;
  width: 120px;
  display: inline-block;
  padding-top: 70px;
  cursor: pointer;
  text-align: center;
  font-weight: 500;
  font-size: 17px;
}
.sub {
  z-index: 999;
  position: absolute;
  top: 110px;
  height: 120px;
  width: 120px;
  background-color: rgba(255, 255, 255, .8);
  overflow: hidden;
  -webkit-animation: enter .5s;
          animation: enter .5s;
}
@-webkit-keyframes enterPro {
from {
    height: 0;
}
to {
    height: 90px;
}
}
@keyframes enterPro {
from {
    height: 0;
}
to {
    height: 90px;
}
}
@-webkit-keyframes enter {
from {
    height: 0;
}
to {
    height: 120px;
}
}
@keyframes enter {
from {
    height: 0;
}
to {
    height: 120px;
}
}



